.footer{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.social_links_container{
  text-align: right;
}

.social_links{
  fill: var(--font_color);
  width: 40px;
  margin-right: 30px;
}

.social_links_container > a:last-of-type >.social_links:last-of-type{
  margin-right: 0;
}